import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import './css/Links.css';

function Links(props) {
  const links = ['/','/aboutus','/library','/contact','/works'];
  const texts = ['top','aboutus','contact','library','works'];
  const linkItems = links.map((item,index) => 
    <>
      <li key={item.id}>
        <Link to={links[index]} >{texts[index]}</Link>
      </li>
    </>
  )
  return(
    <>
      <ul className="links">
        {linkItems}
      </ul>
    </>
  )
}

export default Links;